@font-face {
    font-family: 'lexend_exaregular';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('woff2'),
         url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('woff');
    font-weight: normal;
    font-style: normal;

}

body {
    height: 100vh;
    background: linear-gradient(to bottom, #A1D6B2, #CEDF9F, #F1F3C2);
}

.flx-center {
    display: flex;
    align-items: center;
    justify-content: center;
}

#search-one {
    appearance: none;
    height: 60px;
    padding: 10px;
    border-radius: 30px 0 0 30px;
    width: 500px;
    border: 4px solid rgb(236, 179, 101,.1);
    border-right: 1px solid rgba(74, 73, 71,.2);
}

.search-bar {
    display: flex;
}

.search-img {
    min-height: 100%;
    background-color: white;
    width: 80px;
    border-left: none;
    border-radius: 0 30px 30px 0;
    background: linear-gradient(to right, #F3C623,#EB8317);

    >img {
        height:35px;
        width: auto;
    }
}


input:focus, input:active {
    appearance: none;
    border:0px;
}


table {
    display: none;
    font-family: 'lexend_exaregular', system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    width: 95%;
    margin: auto;
    border-collapse: collapse;
    background-color: rgba(241, 240, 232, 0.6);
}

tr >* {
    border: 1px solid #9EC8B9;
    text-align: center;
}

tr >:not(:last-child){
    width: calc(100% / 18);
}

tr:first-of-type {
    color: #40534C;
}
tr:nth-of-type(2) {
    color: rgba(64, 83, 76,.8);
}

td{
    color: #FBF6EE;
    height: 10px;
    font-size: calc(1rem - 5px);

}


td:not(:first-child) {
    background-color: rgba(251, 246, 238, .3);
    color: rgba(2, 21, 38,.8);
}

th, td {
    height: 45px;
}

nav {
    width: 95%;
    margin: auto;
    display: flex;
    align-items: center;
    height: 90px;
    background-color: rgba(225, 240, 218,.4);
    margin-bottom: 50px;
    display: none;

    >div {
        flex: 1;
        border: 1px solid #EB8317;
    }


#search-one {
    appearance: none;
    height: 40px;
    padding: 10px;
    border-radius: 30px 0 0 30px;
    width: 500px;
    border: 4px solid rgb(236, 179, 101,.1);
    border-right: 1px solid rgba(74, 73, 71,.2);
}
